<template>
    <ol>
        <li v-for="item in items" :key="item.id">
            <span v-if="item.children && item.children.length"><strong>{{ item.name }}</strong> - <small>{{item.member_type.name}}</small></span>
            <span v-else>{{ item.name }} - <small>{{item.member_type.name}}</small></span>
            <NestedVoterList v-if="item.children && item.children.length" :items="item.children" />
        </li>
    </ol>
</template>

<script>
    export default {
        name: "NestedVoterList",
        props: {
            items: Array
        }
    };
</script>
<style scoped>
    span{
        font-size: 16px;
    }
</style>