<template>
    <div>
        <loader :status="is_saving" />
        <b-row class="m-0">
            <b-col cols="1" class="text-center p-2 bg-dark text-white d-flex align-items-center justify-content-center">
                <font-awesome-icon icon="user" class="h2 mb-0" />
            </b-col>
            <b-col class="p-3 bg-facebook text-white d-flex align-items-center">
                <div>
                    <h4 class="mb-0"><strong>{{voter.name}}</strong> </h4>
                    <small class="text-muted text-uppercase font-weight-bold">VOTER</small>
                </div>
            </b-col>
        </b-row>
        <NestedVoterList :items="voter.children" />
    </div>
</template>
<script>
    import SwalMixin from '@/views/mixins/Swal.js'
    import Loader from '@/views/helpers/LoaderComponent.vue';
    import NumberFormatComponent from '@/views/helpers/NumberFormatComponent.vue';
    import { assets_path } from "@/config";
    import {bus} from '@/main.js';

    import NestedVoterList from '@/views/print/NestedVoterList.vue';
    export default {
        mixins:[SwalMixin],
        components: {Loader,NumberFormatComponent,NestedVoterList},
        props:{
            id: {
                type: Number,
                default: -1
            },
        },
        data() {
            return {
                assets_path: {},
                is_saving:false,
                voter:{},
            }
        },
        computed:{
        },
        created(){
            this.id=this.$route.params.id;
            this.assets_path = assets_path;
            this.get();
        },
        methods: {
            get(){
                this.is_saving=true;
                this.membersTableKey++;
                this.axios.get('voters/get',{params:{id:this.id}}).then(response => {
                    //console.log(result)
                    this.voter = response.data;
                    this.is_saving=false;
                }).catch(error => console.log(error));
            },

        }
    }
</script>